$color-bg: #222E33;
$color-1: #253237;
$color-2: #5C6B73;
$color-3: #9DB4C0;
$color-4: #C2DFE3;
$color-5: #E0FBFC;
$color-error: #F53F36;
$color-success: #25A857;

$spacing: 16px;

body {
  background-color: $color-bg;
  color: $color-4;
  font-family: Arial, Helvetica, sans-serif;
}

P {
  margin-bottom: 0;
  margin-top: 0;

  & + & {
    margin-top: $spacing / 2;
  }
}

ul {
  border-top: 1px solid $color-2;
  list-style: none;
  margin-bottom: 0;
  margin-top: $spacing;
  padding-top: $spacing;
  padding-left: 0;
}

li {
  & + & {
    margin-top: $spacing/2;
  }
}

h1 {
  margin: 0;
}

.u-bold {
  font-weight: 700;
}

.u-big {
  font-size: 1.5em;
  line-height: 1;
}

.u-center {
  text-align: center;
}

.u-error {
  color: $color-error;
}

.u-success {
  color: $color-success;
}

.header,
.footer {
  background-color: $color-1;
  border: 1px solid $color-2;
  margin: 0 $spacing;
  padding: $spacing;
}

.header {
  margin-top: $spacing;
}

.footer {
  margin-bottom: $spacing;
}

.grid {
  display: grid;
  gap: $spacing;
  grid-template-columns: 1fr;
  padding: $spacing;

  @media (min-width: 25em) {
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  }

  @media (min-width: 75em) {
    grid-template-columns: repeat(3, 1fr);
  }
}

.grid__box-container {
  background-color: red;
  background-color: $color-1;
  border: 1px solid $color-2;
  padding: $spacing;
}

.grid__box-totals {
  &--toggle {
    cursor: pointer;
    position: relative;
    &::after {
      background-image: url('../img/arrow.svg');
      content: '';
      height: $spacing*1.5;
      position: absolute;
      right: 0;
      top: $spacing;
      transform: rotateZ(90deg);
      width: $spacing*1.5;
    }
  }

  &--opened {
    &::after {
      transform: rotateZ(-90deg);
    }

    + .grid__box-list {
      display: block;
    }
  }
}

.grid__box-list {
  display: none;
}
